html, body, #root {
  height: 100%;
}

.home {
  color: #fff;
  background: radial-gradient(1200px at 50% 0, #ffffff0f, #0000 80%), #0a0a0f;
  min-height: 100vh;
  position: relative;
  overflow: hidden;
}

.home__content {
  z-index: 1;
  text-align: center;
  place-items: center;
  min-height: 100vh;
  padding: 2rem;
  display: grid;
  position: relative;
}

.home__content h1 {
  margin-bottom: .5rem;
  font-size: 3rem;
}

.home__content p {
  opacity: .8;
}
